import React, { forwardRef } from "react";

import {
	StyledButtonList,
} from "./ButtonGroup.styles";
import { StyledButtonListItem } from "./ButtonGroup.styles";
import {
	ButtonGroupButtonProps,
	ButtonGroupProps,
} from "./ButtonGroup.types";
import ButtonGroupButton from "./ButtonGroupButton";
import ButtonGroupButtonWithDropDown from "./ButtonGroupButtonWithDropDown";

const ButtonGroup = forwardRef<HTMLButtonElement, ButtonGroupProps>(({
	dataQA,
	ariaLabel,
	className,
	buttons,
	mt,
	mb,
	ml,
	mr,
	m,
	buttonSize = "medium",
}, ref) => {
	const buttonLength = buttons.length;
	// must have at least two buttons to render
	if (buttonLength < 2) {
		throw new Error("ButtonGroup requires at least two ButtonGroupButton to render");
	}

	return (
		<StyledButtonList
			role="group"
			data-qa={dataQA}
			aria-label={ariaLabel}
			className={className}
			mt={mt}
			mb={mb}
			ml={ml}
			mr={mr}
			m={m}
		>
			{buttons.map((buttonProps: ButtonGroupButtonProps) => (
				<StyledButtonListItem
					data-qa={`${buttonProps.dataQA}-list-item`}
					key={buttonProps.buttonText || buttonProps.icon}
					isSelected={buttonProps.isSelected}
				>
					{buttonProps.dropDownMenuProps ? (
						<ButtonGroupButtonWithDropDown buttonSize={buttonSize} ref={ref} {...buttonProps} />

					) : (
						<ButtonGroupButton buttonSize={buttonSize} ref={ref} {...buttonProps} />)}

				</StyledButtonListItem>
			))}
		</StyledButtonList>
	);
});

ButtonGroup.displayName = "ButtonGroup";
export default ButtonGroup;

